import _ from 'lodash';
import React from 'react';
import { Stack, ToggleButton, ToggleButtonGroup, Button, ButtonGroup, ButtonToolbar, Container, Row, Col, Card } from 'react-bootstrap';
import * as Icon from 'react-bootstrap-icons';

const classColors = [
    'rgb(255, 0, 0)',
    'rgb(0, 255, 0)',
    'rgb(0, 0, 255)'
]

export default class Plotter extends React.Component {
    static defaultProps = {
        points: [],
        onChange: _.noop
    }

    constructor(props) {
        super(props)

        this.state = {
            action: this.props['defaultAction'] ?? "add-red"
        }
    }

    addPointForEvent(e) {
        const img = e.target
        const rect = img.getBoundingClientRect();
        let datumClass = 0
        if (this.state['action'] === "add-green") {
            datumClass = 1
        } else if (this.state['action'] === "add-blue") {
            datumClass = 2
        }

        return {
            x: (e.clientX - rect.left) * (img.naturalWidth / img.width),
            y: (e.clientY - rect.top) * (img.naturalHeight / img.height),
            clientX: e.clientX - rect.left,
            clientY: e.clientY - rect.top,
            width: img.width,
            height: img.height,
            class: datumClass,
            key: _.uniqueId()
        }
    }

    handlePlotClick(e) {
        this.props.onChange([...this.props.points, this.addPointForEvent(e)], 'add')
    }

    handleClear() {
        this.props.onChange([], 'clear')
    }

    handleDragStart(e) {
        this.dragged = e.target
        e.target.style.opacity = 0.5
    }

    handleDragEnd(e) {
        e.target.style.opacity = ""
    }

    handleDrop(e) {
        const key = this.dragged.dataset.key
        let points = this.props.points
        points = [...points, this.addPointForEvent(e)]
        points = _.without(points, _.find(points, { key }))

        this.props.onChange(points, 'drag')
    }

    toolbar() {
        return (
            <div className="toolbar">
                <ButtonToolbar vertical>
                    <ButtonGroup vertical>
                        <Button onClick={e => this.handleClear()} variant="outline-dark">
                            <Icon.EraserFill />
                        </Button>
                    </ButtonGroup>
                    <ButtonGroup vertical>
                        <Button onClick={e => this.handleClear()} variant="outline-dark">
                            <Icon.EraserFill />
                        </Button>
                    </ButtonGroup>
                </ButtonToolbar>
            </div>
        )
    }

    render() {
        const addedPoints = this.props['points'].map(p => {
            return (
                <div draggable={true}
                    onDragStart={(e) => this.handleDragStart(e)}
                    onDragEnd={(e) => this.handleDragEnd(e)}
                    className="point"
                    key={p.key}
                    data-key={p.key}
                    style={{
                        width: `${9/window.innerWidth * 100}vw`,
                        height: `${9/window.innerWidth * 100}vw`,
                        left: `${(p['clientX'] - 4) / p['width'] * 100}%`,
                        top: `${(p['clientY'] - 4) / p['height'] * 100}%`,
                        position: 'absolute',
                        border: '2px solid black',
                        backgroundColor: classColors[p['class']],
                        opacity: 1,
                        cursor: 'grab'
                    }}></div>
            )
        })

        return (
            <div className="plotter">
                <Container>
                    <Row>
                        <Col>
                            <Card>
                                <Card.Body className="img-container">
                                    {addedPoints}
                                    <img src={this.props['content']}
                                        alt={this.props['alt']}
                                        style={{ cursor: "crosshair" }}
                                        onClick={e => this.handlePlotClick(e)}
                                        onDrop={e => this.handleDrop(e)}
                                        onDragOver={e => e.preventDefault()}
                                    />
                                </Card.Body>
                            </Card>
                        </Col>
                        {this.toolbar()}

                    </Row>
                </Container>
            </div>
        );
    }
}